import { _decorator, Component, Node } from 'cc';
import WebUtils from '../../utils/WebUtils';
const { ccclass, property } = _decorator;

@ccclass('SceneBgComponent')
export class SceneBgComponent extends Component {
    @property({
        type: [Node]
    })
    public bgs: Node[] = [];
    @property({
        type: [String]
    })
    public bgsPicname: String[] = [];
    start() {
        this.loadPic();
    }
    private loadPic() {
        for (let index = 0; index < this.bgs.length; index++) {
            const element = this.bgs[index];
            WebUtils.getResouceImg("resize/" + this.bgsPicname[index], element);
        }
    }

    update(deltaTime: number) {

    }
}


